import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from './pages/Home';
import Contact from './pages/Contact';
function App() {
	return (
		<Router>
			<div id="fullWeb">
				<Switch>
					<Route path="/">
						<Home />
					</Route>
					<Route path="/contact">
						<Contact />
					</Route>
				</Switch>
			</div>
		</Router>
	);
}
export default App;
